import axios from 'axios'
export default {
  name: 'checkGainDetail',
  data () {
    return {
      gainName: '',
      okGain: false,
      gain: '',
      file: '',
      gainID: sessionStorage.getItem('gainID')
    }
  },
  methods: {
    getGain () {
      axios.get(this.url + '/gain/id/' + sessionStorage.getItem('gainID'))
        .then(this.getGainSucc)
    },
    getGainSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.gain = res
        this.gainName = res.name
      }
    },
    downFileUrl () {
      return 'http://bci.renjiwulian.com/gain/file/' + this.gainID
    },
    downFile () {
      window.location.href = 'http://bci.renjiwulian.com/gain/file/' + this.gainID
    },
    downFileSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.file = res
      }
    },
    submit () {
      this.$axios({
        method: 'put',
        url: this.url + '/gain/',
        data: {
          id: parseInt(sessionStorage.getItem('gainID')),
          state: this.GainYes
        }
      }).then((res) => {
        if (res.status === 200) {
          alert('提交成功！')
          this.$router.push('/check')
        } else alert(res.data)
      }).catch((err) => {
        alert(err)
      })
      this.okGain = false
    }
  },
  mounted () {
    this.getGain()
  }
}



